import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./Home.module.scss"
import AboutPageContent from "../components/AboutPageContent"
import ContactPageContent from "../components/ContactPageContent"
import WorkPageContent from "../components/WorkPageContent"

export default () => {
  const { hero, avatarText } = useStaticQuery(graphql`
    query {
      hero: file(relativePath: { eq: "hero.png" }) {
        childImageSharp {
          fluid(quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      avatarText: file(relativePath: { eq: "avatarText.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.hero}>
        <Img
          fluid={hero.childImageSharp.fluid}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
        />
        <div className={styles.heroContent}>
          <Img
            fixed={avatarText.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
          <div className={styles.sologan}>
            <span>Simple&nbsp;</span>
            <span className={styles.sologanSmallText}>but&nbsp;</span>
            <div>
              <span>Diff</span>
              <span className={styles.sologanSpecialText}>e</span>
              <span>rent</span>
            </div>
          </div>
        </div>
      </div>
      <AboutPageContent />
      <WorkPageContent />
      <ContactPageContent />
    </React.Fragment>
  )
}
